/**** standard input ****/
.standard-input {
  font-size: 18px;
  padding: 10px 10px 10px 0px;
  display: block;
  width: 300px;
  border: none;
  border-bottom: 1px solid #757575;
  transition: all 100ms;
}

.standard-input:focus {
  outline: none;
}

.standard-input-label,
.standard-input-label-err {
  color: #999;
  font-size: 18px;
  font-weight: normal;
  position: absolute;
  pointer-events: none;
  top: 13px;
  transition: 0.2s ease all;
}

.standard-input:focus ~ .standard-input-label,
.standard-input:valid ~ .standard-input-label {
  top: -15px;
  font-size: 14px;
  color: #1976d2;
}

.standard-input:focus ~ .standard-input-label-err,
.standard-input:valid ~ .standard-input-label-err {
  top: -15px;
  font-size: 14px;
  color: #f44336;
}

.standard-input-bar {
  position: relative;
  display: block;
  width: 300px !important;
}

.standard-input-bar-err {
  position: relative;
  display: block;
  width: 300px !important;
}

.standard-input-bar:before,
.standard-input-bar:after {
  content: '';
  height: 2px;
  width: 0;
  bottom: -0.5px;
  position: absolute;
  background: #1976d2;
  transition: 0.2s ease all;
}

.standard-input-bar-err:before,
.standard-input-bar-err:after {
  content: '';
  height: 2px;
  width: 0;
  bottom: -0.5px;
  position: absolute;
  background: #f44336;
  transition: 0.2s ease all;
}

.standard-input-bar:before {
  left: 50%;
}

.standard-input-bar-err:before {
  left: 50%;
}
.standard-input-bar:after {
  right: 50%;
}

.standard-input-bar-err:after {
  right: 50%;
}

.standard-input:focus ~ .standard-input-bar:before,
.standard-input:focus ~ .standard-input-bar:after {
  width: 50% !important;
}

.standard-input:focus ~ .standard-input-bar-err:before,
.standard-input:focus ~ .standard-input-bar-err:after {
  width: 50% !important;
}
/**** standard input ****/

